import React from 'react';
import './App.css';

//App

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      values: {
        "Day 1": {}
      },
      days: 1
    };
    this.saveDay = this.saveDay.bind(this);
    this.handleCrunchesChange=this.handleCrunchesChange.bind(this);
    this.handleSquatsChange=this.handleSquatsChange.bind(this);
    this.handlePushUpsChange=this.handlePushUpsChange.bind(this);
    this.handleRunningChange=this.handleRunningChange.bind(this);
    this.handleWeightChange=this.handleWeightChange.bind(this);
  };

  componentDidMount() {
    if (typeof (Storage) !== "undefined") {

      const data = JSON.parse(localStorage.getItem('data'));
  
      this.setState(data);
  }
  else {
      alert("Sorry, your browser does not support web storage...");
  }
  }

  saveDay() {
    this.setState({days: this.state.days + 1});
    let values=this.state.values;
    values[`Day ${this.state.days + 1}`] = {};
    this.setState({values: values});
    //local storage

    if (typeof (Storage) !== "undefined") {
      const data = JSON.stringify(this.state);
      localStorage.setItem('data', data);
    } else {
      alert("Sorry, your browser doesn't support local storage");
    };
  }

  handleCrunchesChange(num, id) {
    const prevValues=this.state.values;
    const pair={"Crunches": num};
    Object.assign(prevValues[`Day ${id}`], pair);
    this.setState({values: prevValues});
  }

  handleSquatsChange(num, id) {
    const prevValues=this.state.values;
    const pair={"Squats": num};
    Object.assign(prevValues[`Day ${id}`], pair);
    this.setState({values: prevValues});
  }

  handlePushUpsChange(num, id) {
    const prevValues=this.state.values;
    const pair={"Push-ups": num};
    Object.assign(prevValues[`Day ${id}`], pair);
    this.setState({values: prevValues});
  }

  handleRunningChange(num, id) {
    const prevValues=this.state.values;
    const pair={"Running": num};
    Object.assign(prevValues[`Day ${id}`], pair);
    this.setState({values: prevValues});
  }

  handleWeightChange(num, id) {
    const prevValues=this.state.values;
    const pair={"Weight": num};
    Object.assign(prevValues[`Day ${id}`], pair);
    this.setState({values: prevValues});
  }

  render() {
    const children = [];

    for (let i = 1; i<this.state.days; i+=1) {
      children.push(<DayCompleted key={i} day={i} crunches={this.state.values[`Day ${i}`]["Crunches"]} squats={this.state.values[`Day ${i}`]["Squats"]} pushups={this.state.values[`Day ${i}`]["Push-ups"]} running={this.state.values[`Day ${i}`]["Running"]} weight={this.state.values[`Day ${i}`]["Weight"]}/>)
    }
  
    children.push(<Day key={this.state.days} day={this.state.days} onCrunchesChange={this.handleCrunchesChange} 
      onSquatsChange={this.handleSquatsChange} onPushUpsChange={this.handlePushUpsChange}
      onRunningChange={this.handleRunningChange} onWeightChange={this.handleWeightChange}/>)

  return (
    <div className="App">
      <h1>Saitama Training Tracker</h1>
      {children}
      <button id="save" onClick={this.saveDay}>Save Day</button>
    </div>
  );
  }
}

export default App;

//components

class DayCompleted extends React.Component {
  render() {
    return(
      <div className=" day completed">
        <span className="impact">Day {this.props.day}:</span>
        <br/><span className="crunches">Crunches: {this.props.crunches}/100</span> <span className="squats">Squats: {this.props.squats}/100</span>
        <br/><span className="pushUps">Push-ups: {this.props.pushups}/100</span> <span className="running">Running: {this.props.running}/10km</span>
        <br/><span className="weight">Weight: {this.props.weight} kg</span>
      </div>
    )
  }
}

class Day extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      day: this.props.days,
    };
    this.handleCrunchesChange=this.handleCrunchesChange.bind(this);
    this.handleSquatsChange=this.handleSquatsChange.bind(this);
    this.handlePushUpsChange=this.handlePushUpsChange.bind(this);
    this.handleRunningChange=this.handleRunningChange.bind(this);
    this.handleWeightChange=this.handleWeightChange.bind(this);
  }

  handleCrunchesChange(num) {
    this.props.onCrunchesChange(num, this.props.day);
  }

  handleSquatsChange(num) {
    this.props.onSquatsChange(num, this.props.day);
  }

  handlePushUpsChange(num) {
    this.props.onPushUpsChange(num, this.props.day);
  }

  handleRunningChange(num) {
    this.props.onRunningChange(num, this.props.day);
  }

  handleWeightChange(num) {
    this.props.onWeightChange(num, this.props.day);
  }

render() {
  return (
    <div className = "day">
      <span className= "impact">Day {this.props.day}:</span> 
      <br/> <Crunches onChange={this.handleCrunchesChange}/> <Squats onChange={this.handleSquatsChange}/> 
      <br/> <PushUps onChange={this.handlePushUpsChange}/> <Running onChange={this.handleRunningChange}/> 
      <br/> <Weight onChange={this.handleWeightChange}/>
    </div>
  )
}

}

class Crunches extends React.Component {
  constructor(props) {
    super(props);
    this.handleChange = this.handleChange.bind(this);
  }

  handleChange(e) {
    this.props.onChange(e.target.value);
  }

  render () {
    return(
      <div className="crunches">
        <label>Crunches:</label>
        <input type="number" min={0} step={1} onChange={this.handleChange}></input>/100
      </div>
    )
  }

}

